@use 'base/variables' as v;
@use 'base/mixins' as m;

.testimoniales {

    @include m.tablet {
        @include m.grid(5, 4rem);
        align-items: center;
    }

    .imagen {
        grid-column: 1 / 3;
        margin-bottom: 5rem;
        @include m.tablet {
            margin-bottom: 0;
        }

        img {
            margin-inline: auto;
        }
    }

    .contenido {
        grid-column: 3 / 6;
    }

    h2 {
        @include m.headingBorder(v.$secundario, v.$primario);
    }

}
.contenido {

    .testimonial {
        background-color: v.$primario;
        margin-bottom: 4rem;
        border: 0.5rem solid v.$secundario;
        padding: 2rem;
        border-radius: 1rem;
        position: relative;

        @include m.tablet {
            max-width: 85%;

            &:nth-child(2) {
                margin-left: 5%;
            }
            &:nth-child(3) {
                margin-left: 10%;
            }
            &:nth-child(4) {
                margin-left: 15%;
            }
        }
        &::before {
            content: ' ';
            display: block;
            width: 5rem;
            height: 5rem;
            background-image: url('../img/comilla.svg');
            background-size: 5rem;
            background-repeat: no-repeat;
            background-position: center;
            position: absolute;
        }
        &:last-child {
            margin-bottom: 0;
        }
        p {
            margin: 0;
            padding-left: 6rem;
        }
    }

}